import Vue from 'vue'
import Router from 'vue-router'

// Containers
import Profile from '@/views/Profile'
const Dashboard = () => import('../modules/dashboard') // lazy loading
const Offers = () => import('../modules/dashboard/components/offers')
const SupplierContainer = () => import('../modules/supplier')
const SupplierOffers = () => import('../modules/supplier/components/offers')
const OfferApplications = () => import('../modules/supplier/components/offers/Applications')
Vue.use(Router)

export default new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/dashboard',
      name: 'Home',
      component: Profile,
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          component: Dashboard
        },
        {
          path: 'offers',
          name: 'AllOffers',
          component: Offers
        }

      ]
    },
    {
      path: '/supplier',
      name: 'supplier_view',
      component: SupplierContainer,
      children: [
        {
          path: '',
          name: 'offers',
          component: SupplierOffers
        },
        {
          path: 'offers/:id/applications',
          component: OfferApplications
        }
      ]
    }
  ]
})



